.Management {
    background-color: black;
    color: white;
    padding: 0 2rem;
    overflow: hidden;
    display: flex;
    flex-direction: column;
    height: 100vh;
}

.Management .header {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 1rem 0;
}

.Management .header h1 {
    margin: 0;
    font-size: 1.2rem;
    font-weight: 400;
    display: flex;
    align-items: center;
    gap: 1rem;
}

.Management .header .button {
    font-family: inherit;
    font-size: 1rem;
    text-decoration: none;
    font-weight: 500;
    background-color: #ffc849;
    border: 0;
    border-radius: 5px;
    padding: 10px 2rem;
    color: black;
    font-weight: 500;
    cursor: pointer;
    display: flex;
    align-items: center;
    gap: 10px;
}

.Management .notice {
    background: #ffc849;
    color: black;
    border-radius: 5px 5px 0 0;
    padding: 4px;
    text-align: center;
    width: 100%;
    font-weight: 500;
    font-size: 15px;
    display: flex;
    justify-content: center;
    align-items: center;
    gap: 10px;
}

.Management iframe {
    width: 100%;
    height: 100%;
    display: block;
    border: none;
    border-radius: 0 0 5px 5px;
    border-left: 2px solid #ffc849;
    border-right: 2px solid #ffc849;
    border-bottom: 2px solid #ffc849;
}

.Management .iframe-loader {
    width: 100%;
    height: 100%;
    background: #111;
    background-image: radial-gradient(
            circle at 40% 20%,
            rgba(255, 140, 245, 0.03) 20%,
            transparent 40%
        ),
        radial-gradient(circle at 60% 50%, rgba(61, 177, 160, 0.05) 100%, transparent 40%),
        radial-gradient(circle at 40% 60%, rgba(35, 44, 142, 0.1) 20%, transparent 80%);
    border: none;
    border-radius: 0 0 5px 5px;
    font-size: 2rem;
    color: white;
    display: flex;
    justify-content: center;
    align-items: center;
}

.Management .iframe-loader .loader {
    width: 2rem;
    height: 2rem;
    margin-right: 1rem;
    border: 3px solid #ffcc00;
    border-bottom-color: transparent;
    border-radius: 50%;
    display: inline-block;
    box-sizing: border-box;
    animation: rotation 1s linear infinite;
}

@keyframes rotation {
    0% {
        transform: rotate(0deg);
    }
    100% {
        transform: rotate(360deg);
    }
}
